export default {
	data() {
		return {
			title: 'brightness',
			screen: 0,
			keepScreenOn: true
		};
	},
	onLoad() {
		uni.getScreenBrightness({
			success: res => {
				console.log(res);
				this.screen = (res.value * 100).toFixed();
				console.log('屏幕亮度值：' + this.screen);
			},
			fail(e) {
				console.log(e);
			}
		});
	},
	methods: {
		sliderChange(e) {
			let screen = e.detail.value;
			// 判断是否重复
			if (this.screen !== screen) {
				console.log('当前数值：' + e.detail.value);
				uni.setScreenBrightness({
					value: screen / 100,
					success: function() {
						console.log('success');
					},
					fail(e) {
						console.log(e);
					}
				});
				this.screen = screen;
			}
		},
		keep() {
			uni.setKeepScreenOn({
				keepScreenOn: this.keepScreenOn
			});
			this.keepScreenOn = !this.keepScreenOn;
		}
	}
};
